
import { defineComponent } from 'vue'
export default defineComponent({
  props: {
    questionsText: {
      type: String,
      required: true
    },
    halfTimes: {
      type: Number,
      required: true
    },
    searchTimes: {
      type: Number,
      required: true
    }
  },
  emits: ['handle-hint'],
  setup (_, { emit }) {
    function handleHint (type: string) {
      emit('handle-hint', type)
    }

    return {
      handleHint
    }
  }
})
